    <!--提示-->
    <!--<el-dialog title="" :visible.sync="tipVisible" :close-on-click-modal="false">-->
      <!--<div class="title">提示</div>-->
      <!--<div class="content">奖励个数添加不足! <br>请确保已设置10个奖励后再发布!</div>-->
