        <!--<div class="goods" ref="goods"></div>-->
        <!--<div class="goods" ref="goods"></div>-->
      </div>
    </el-dialog>
  </el-row>

</template>

<script>
    import axios from 'axios'
    export default {
        name: "Cq_lottery",
        data(){
          return{
            adjustmentVisible:false,
            addVisible:false,
            imageUrl: '',
            addFormRules: {
              title: [
                {required: true, message: '请输入标题', trigger: 'blur'}
              ],
              file: [
                {required: true, message: '请选择图片', trigger: 'blur'}
              ],
              address: [
                {required: true, message: '请选择图片', trigger: 'blur'}
              ]
            },
            adjustForm:{},
            addForm:{},
            items:[
              {value:'玉石'},
              {value:'会员周卡'},
              {value:'会员月卡'},
              {value:'会员季卡'},
              {value:'红包点'},
              {value:'实物'},
            ],
            types:[
              {value:'红包'},
              {value:'会员卡'},
              {value:'玉石'},
              {value:'实物'},
              {value:'话费'},
            ],
            lottery:[],
            filters:{},
            // 七牛云的上传地址，根据自己所在地区选择，这里是华东区
            domain: 'http://up.qiniu.com',
            // 这是七牛云空间的外链默认域名
            qiniuaddr: 'qiniu.3cscy.com',
            value:'',
            tipVisible:true,
            previewVisible:false
          }
        },
      methods:{
        // 上传文件到七牛云
        upqiniu(req) {
          console.log(req)
          const config = {
            headers: {'Content-Type': 'multipart/form-data'}
          }
          let filetype = ''
          if (req.file.type === 'image/png') {
            filetype = 'png'
          } else {
            filetype = 'jpg'
          }
          // 重命名要上传的文件
          const keyname = 'anbang' + Math.random() + '.' + filetype
          // 从后端获取上传凭证token
          axios({
            method: 'post',
            url: this.global.mPath + '/mailctrl/uptoken',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
          }).then(res => {
            const formdata = new FormData()
            formdata.append('file', req.file)
            formdata.append('token', res.data.data)
            formdata.append('key', keyname)
            // 获取到凭证之后再将文件上传到七牛云空间
            axios.post(this.domain, formdata, config).then(res => {
              this.imageUrl = 'http://' + this.qiniuaddr + '/' + res.data.key
              this.img = false;
              console.log(this.imageUrl)
            })
          })
        },
        // 验证文件合法性
        beforeUpload(file) {
          const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
          const isLt2M = file.size / 1024 / 1024 < 2
          if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!')
          }
          if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!')
          }
          return isJPG && isLt2M
        },
        adjustmentDialog:function(){
          this.adjustmentVisible = true;
        },
        showAddDialog:function(){

        },
        AddDialog:function(){
          this.addVisible = true;
        },
        viewPreview:function(){
          this.previewVisible = true;
          // let aa = document.getElementsByClassName('content')[0];
          // console.log(this.$refs.goods.length)
          // for(let i = 0; i < aa.length;i++){
          //   console.log(111)
          //   aa[i].style.transform = 'rotate(' + 360/10 * (i+1) + 'deg)skewX(54deg)'
          // }
        },

      },
      mounted(){
        // console.log()
        // this.$refs.aa.style.backgroundColor = 'red'
      }
    }
</script>

